import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const codeButton_main = `
<button am-button="main"> </button>
<button am-button="main" disabled> </button>
`;

const codeButton_hover = `
<button am-button="white"> </button>
<button am-button="white" disabled> </button>
`;

const codeButton_disabled = `
<button am-button="disabled"> </button>
<a am-button="disabled"> </a>
<div am-button="disabled"> </div>
`;

const codeButton_default = `
<button am-button> </button>
<a am-button> </a>
<div am-button> </div>
`;

const scssVariables = `
// Colors
$color-main: #ff0000;
$color-main-dark: #D9000D;
$color-main-gray: #A3A2A1;
`;

export class Button extends Component {
    constructor(props) {
        super(props)

        this.state = {
            codeButton_main,
            codeButton_hover,
            codeButton_disabled,
            codeButton_default,
            scssVariables
        }

        this.codeButton_main = React.createRef()
        this.codeButton_hover = React.createRef()
        this.codeButton_disabled = React.createRef()
        this.codeButton_default = React.createRef()
    }

    componentDidMount() {
        this.codeButton_main.current.innerHTML = this.state.codeButton_main;
        this.codeButton_hover.current.innerHTML = this.state.codeButton_hover;
        this.codeButton_disabled.current.innerHTML = this.state.codeButton_disabled;
        this.codeButton_default.current.innerHTML = this.state.codeButton_default;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Button' }
                    headerText={ ' .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-button.scss';" />  
                    </div>        
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import '@ui/scss/am-button.scss'" />  
                    </div>
                </Header>
                <Content>
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview" ref={this.codeButton_main}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.codeButton_main}
                                            onValueChange={codeButton_main => { this.setState({ codeButton_main }); this.codeButton_main.current.innerHTML = codeButton_main}}
                                            readOnly={true}
                                            highlight={codeButton_main => highlight(codeButton_main, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview" ref={this.codeButton_hover}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.codeButton_hover}
                                            onValueChange={codeButton_hover => { this.setState({ codeButton_hover }); this.codeButton_hover.current.innerHTML = codeButton_hover}}
                                            readOnly={true}
                                            highlight={codeButton_hover => highlight(codeButton_hover, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview" ref={this.codeButton_disabled}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.codeButton_disabled}
                                            onValueChange={codeButton_disabled => { this.setState({ codeButton_disabled }); this.codeButton_disabled.current.innerHTML = codeButton_disabled}}
                                            readOnly={true}
                                            highlight={codeButton_disabled => highlight(codeButton_disabled, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview" ref={this.codeButton_default}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.codeButton_default}
                                            onValueChange={codeButton_default => { this.setState({ codeButton_default }); this.codeButton_default.current.innerHTML = codeButton_default}}
                                            readOnly={true}
                                            highlight={codeButton_default => highlight(codeButton_default, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <h2 className="example__header">scss .</h2>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div>                                
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}