import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';
import './input.scss';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const input = `
<div am-input>
    <input type="text"/>
</div>

<div am-input accept>
    <input type="text" placeholder=" "/>
</div>

<div am-input error>
    <input type="text" placeholder=" "/>
</div>
`;

const input_label = `
<div am-input="name">
    <div am-hint="top"></div>
    <input name="name" type="text" placeholder="">
</div>
`;

const input_validate = `
<div am-input="name" accept>
    <div am-hint="top"></div>
    <div am-input-group>
        <div am-check></div>
        <input name="name" type="text" placeholder="">
    </div>
</div>

<div am-input="name" error>
    <div am-hint="top"> <span am-required="">*</span></div>
    <div am-input-group>
        <div am-check></div>
        <input name="name" type="text" placeholder="">
    </div>
    <div am-hint="bottom"><span am-required="">*</span>   </div>
</div>
`;

const input_search = `
<div am-search>
    <div am-search-icon>
        <span am-icon="a_search_dark"></span>
    </div>
    <input name="search" type="text" placeholder="">
    <button am-search-button></button>
</div>
`;

const textarea = `
<div am-input="message">
    <div am-hint="top"></div>
    <div am-input-group>
        <div am-check></div>
        <textarea name="message" rows="7" placeholder=" ."></textarea>
    </div>
</div>
`;

const scssVariables = `
// Colors
$color-main-gray-light: #E6E4E1 !default;
$color-main-green: #94F57C !default;
$color-main: #ff0000 !default;
$color-main-dark: #D9000D !default;
$color-main-gray: #A3A2A1 !default;
`;

export class Input extends Component {
    constructor(props) {
        super(props)

        this.state = {
            input,
            input_label,
            input_validate,
            input_search,
            textarea,
            scssVariables
        }

        this.input = React.createRef()
        this.input_label = React.createRef()
        this.input_validate = React.createRef()
        this.input_search = React.createRef()
        this.textarea = React.createRef()
    }

    componentDidMount() {
        this.input.current.innerHTML = this.state.input;
        this.input_label.current.innerHTML = this.state.input_label;
        this.input_validate.current.innerHTML = this.state.input_validate;
        this.input_search.current.innerHTML = this.state.input_search;
        this.textarea.current.innerHTML = this.state.textarea;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Input' }
                    headerText={ '  .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-input.scss';" />  
                    </div>        
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import '@ui/scss/am-input.scss'" />  
                    </div>
                </Header>
                <Content>
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.input}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.input}
                                            readOnly={true}
                                            onValueChange={input => { this.setState({ input }); this.input.current.innerHTML = input}}
                                            highlight={input => highlight(input, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview" ref={this.input_label}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.input_label}
                                            readOnly={true}
                                            onValueChange={input_label => { this.setState({ input_label }); this.input_label.current.innerHTML = input_label}}
                                            highlight={input_label => highlight(input_label, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.input_validate}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.input_validate}
                                            readOnly={true}
                                            onValueChange={input_validate => { this.setState({ input_validate }); this.input_validate.current.innerHTML = input_validate}}
                                            highlight={input_validate => highlight(input_validate, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.input_search}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.input_search}
                                            readOnly={true}
                                            onValueChange={input_search => { this.setState({ input_search }); this.input_search.current.innerHTML = input_search}}
                                            highlight={input_search => highlight(input_search, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">Textarea</h2>
                                    <div className="example__preview" ref={this.textarea}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.textarea}
                                            readOnly={true}
                                            onValueChange={textarea => { this.setState({ textarea }); this.textarea.current.innerHTML = textarea}}
                                            highlight={textarea => highlight(textarea, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <h2 className="example__header">scss .</h2>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div> 
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}