import React from 'react'
import './BurgerButton.scss'

function burgerButtonHanlder(e, click) {
    const button = e.target.closest('[burger-button]')

    if (button.hasAttribute('active')) {
        button.removeAttribute('active')
        click(false)
    } else {
        button.setAttribute('active', '')
        click(true)
    }
}

const BurgerButton = props => {
    if (props.active) {
        return (
            <div burger-button={''} active={''} onClick={e => {burgerButtonHanlder(e, props.click)}}>
                <div burger-line={''}></div>
                <div burger-line={''}></div>
                <div burger-line={''}></div>
            </div>
        )
    } else {
        return (
            <div burger-button={''} onClick={e => {burgerButtonHanlder(e, props.click)}}>
                <div burger-line={''}></div>
                <div burger-line={''}></div>
                <div burger-line={''}></div>
            </div>
        )
    }
}

export default BurgerButton