import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'
import TooltipUI from '@ui/Tooltip'

const scssVariables = `
// Tooltip
$zIndex-tooltip: 99 !default;
`;

const tooltip = `
<div am-tooltip>
    <div am-tooltip-header>tooltip</div>
    <div am-tooltip-content>
        <div am-tooltip-wrapper>
            <div am-tooltip-arrow></div>
            <ul>
                <li>tooltip-item-1</li>
                <li>tooltip-item-2</li>
                <li>tooltip-item-3</li>
                <li>tooltip-item-4</li>
                <li>tooltip-item-5</li>
            </ul>
        </div>
    </div>
</div>
`;

const tooltip_direction = `
<div am-grid="container" spacing="1">
    <div am-grid="item" elg="3" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip-bottom (default)</div>
            <div am-tooltip-content direction="bottom">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow></div>
                    <ul>
                        <li>
                            <a href="/uikit/components/tooltip">/uikit/components/tooltip</a>
                        </li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="3" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip-left</div>
            <div am-tooltip-content direction="left">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="3" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip-top</div>
            <div am-tooltip-content direction="top">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="3" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip-right</div>
            <div am-tooltip-content direction="right">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</div>
`;

const tooltip_align = `
<div am-grid="container" spacing="1">
    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip align-left</div>
            <div am-tooltip-content align="left">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow align="left"></div>
                    <ul>
                        <li>
                            <a href="/uikit/components/tooltip">/uikit/components/tooltip</a>
                        </li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip align-stretch</div>
            <div am-tooltip-content align="stretch">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow align="center"></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip align-right</div>
            <div am-tooltip-content align="right">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow align="right"></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip align-right</div>
            <div am-tooltip-content align="left" direction="top">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow align="left"></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip align-right</div>
            <div am-tooltip-content align="stretch" direction="top">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow align="center"></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-tooltip>
            <div am-tooltip-header>tooltip align-right</div>
            <div am-tooltip-content align="right" direction="top">
                <div am-tooltip-wrapper>
                    <div am-tooltip-arrow align="right"></div>
                    <ul>
                        <li>tooltip-item-1</li>
                        <li>tooltip-item-2</li>
                        <li>tooltip-item-3</li>
                        <li>tooltip-item-4</li>
                        <li>tooltip-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</div>
`;

export class Tooltip extends Component {
    constructor(props) {
        super(props)

        this.state = {
            tooltip,
            tooltip_direction,
            tooltip_align,
            scssVariables
        }

        this.tooltip = React.createRef()
        this.tooltip_direction = React.createRef()
        this.tooltip_align = React.createRef()
    }

    componentDidMount() {
        TooltipUI.bubbleInit()

        this.tooltip.current.innerHTML = this.state.tooltip;
        this.tooltip_direction.current.innerHTML = this.state.tooltip_direction;
        this.tooltip_align.current.innerHTML = this.state.tooltip_align;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Tooltip' }
                    headerText={ '  .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import Dropdown from '@ui/Tooltip'" />  
                    </div>   
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js (scss):</span> <CopyText language="js" copyText="import '@ui/scss/am-tooltip.scss'" />  
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-tooltip.scss';" />  
                    </div>
                </Header>
                <Content className="tooltip">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <li><TabLink to="properties" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                {/* <p className="example__description">       .    ,      .</p> */}
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.tooltip}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.tooltip}
                                            readOnly={true}
                                            onValueChange={tooltip => { this.setState({ tooltip }); this.tooltip.current.innerHTML = tooltip}}
                                            highlight={tooltip => highlight(tooltip, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <p className="example__description">   ,   <span className="token attr-name"> direction</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>bottom|right|top|left<span className="token punctuation">" </span></span>
                                      <span className="token attr-name">am-tooltip-content</span>
                                    </p>
                                    <div className="example__preview" ref={this.tooltip_direction}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.tooltip_direction}
                                            readOnly={true}
                                            onValueChange={tooltip_direction => { this.setState({ tooltip_direction }); this.tooltip_direction.current.innerHTML = tooltip_direction}}
                                            highlight={tooltip_direction => highlight(tooltip_direction, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <p className="example__description">   ,   <span className="token attr-name"> align</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>left|stretch|right<span className="token punctuation">" </span></span>
                                      <span className="token attr-name">am-tooltip-content</span>
                                    </p>
                                    <p className="example__description">   <span className="token attr-name"> direction</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>left|right<span className="token punctuation">"</span></span>
                                    </p>
                                    <div className="example__preview" ref={this.tooltip_align}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.tooltip_align}
                                            readOnly={true}
                                            onValueChange={tooltip_align => { this.setState({ tooltip_align }); this.tooltip_align.current.innerHTML = tooltip_align}}
                                            highlight={tooltip_align => highlight(tooltip_align, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div>
                            </TabContent>
                            <TabContent for="properties" className="tabs__content" tag="content">
                                <div am-table-responsive={''}>
                                    <table am-table={''} border-bottom={''}>
                                        <caption>
                                           Tooltip-content
                                        </caption>
                                        <thead>
                                            <tr>
                                                <th></th>
                                                <th>  </th>
                                                <th></th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>direction</td>
                                                <td>bottom</td>
                                                <td>top&nbsp;| right&nbsp;| left&nbsp;| bottom&nbsp;|</td>
                                                <td></td>
                                            </tr>
                                            <tr>
                                                <td>align</td>
                                                <td>center</td>
                                                <td>left&nbsp;| center&nbsp;| stretch&nbsp;| right&nbsp;|</td>
                                                <td></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}