import React, { Component, Fragment }               from 'react'
import Editor                                       from 'react-simple-code-editor';
import { highlight, languages }                     from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content                                      from '../../../containers/Content.jsx'
import Header                                       from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider }     from '../../../components/ui/Tabs/'

import CopyText                                     from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext }                            from '../../../containers/Layout.jsx';

const js = `
const images = document.querySelectorAll('[am-image]');

images.forEach((item, index) => {
    item.addEventListener('click', (e) => {
        e.preventDefault();

        const gallery = openPhotoSwipe(document.querySelectorAll('.swiper-slide [am-image]'), index);

        gallery.init();
    })
})
`;

const jspswp = `
const openPhotoSwipe = (images, imageIndex) => {
    const pswpElement = document.querySelectorAll('.pswp')[0];

    let imageArray = [];

    images.forEach((item) => {
        imageArray.push(item.getAttribute('src'));
    })

    const items = imageArray.map((item) => {
        const obj = {
            src: item,
            w: 1200,
            h: 800,
        }

        return obj;
    });

    const options = {
        index: imageIndex,
        zoomEl: true,
        getDoubleTapZoom: function (isMouseClick, item) {
            if (isMouseClick) {
                return 2;
            } else {
                return item.initialZoomLevel < 0.5 ? 1 : 1.5;
            }
        },
        getThumbBoundsFn: () => {
            const thumbs = document.querySelectorAll('[am-thumb-item]');
            let thumbArray = [];
            thumbs.forEach(item => {
                thumbArray.push(item);
            });
            const thumbnail = thumbArray[imageIndex];
            let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;
            const rect = thumbnail.getBoundingClientRect();
            return {x: rect.left, y: rect.top + pageYScroll, w: rect.width};
        },
        closeElClasses: ['item', 'caption', 'zoom-wrap', 'ui', 'top-bar']
    }

    const pswp = new PhotoSwipe(pswpElement, PhotoSwipeUI_Default, items, options);

    return pswp;
}
`;

const pswp = `
<div class="pswp" tabindex="-1" role="dialog" aria-hidden="true">
    <div class="pswp__bg"></div>
    <div class="pswp__scroll-wrap">
        <div class="pswp__container">
            <div class="pswp__item"></div>
            <div class="pswp__item"></div>
            <div class="pswp__item"></div>
        </div>
        <div class="pswp__ui">
            <div class="pswp__top-bar">
                <div class="pswp__counter"></div>
                <button class="pswp__button pswp__button--close" title="Close (Esc)"></button>
                <button class="pswp__button pswp__button--fs" title="Toggle fullscreen"></button>
                <button class="pswp__button pswp__button--zoom" title="Zoom in/out"></button>
                <div class="pswp__preloader">
                    <div class="pswp__preloader__icn">
                        <div class="pswp__preloader__cut">
                            <div class="pswp__preloader__donut"></div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
                <div class="pswp__share-tooltip"></div>
            </div>
            <button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)">
            </button>
            <button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)">
            </button>
            <div class="pswp__caption">
                <div class="pswp__caption__center"></div>
            </div>
        </div>
    </div>
</div>
`;

const html = `
<div class="swiper-container">
    <div class="swiper-wrapper">
        <div class="swiper-slide">
            <img src="/modules/image/src/images/cache/product_photo_1_2002000500649_8-705-440.webp" am-image>
        </div>
    </div>
</div>
`

class Photoswipe extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Photoswipe' }
                    headerText={ '.' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import PhotoSwipe from 'photoswipe';" />
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import PhotoSwipeUI_Default from 'photoswipe/dist/photoswipe-ui-default'" />
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/photoswipe/src/css/main.scss';" />
                    </div>
                </Header>
                <Content className="grid">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <p className="example__about"> <a target="_blank"  rel="noreferrer" href="https://photoswipe.com/"></a></p>
                                <div className="example__code">
                                    <Editor
                                        value={'npm i photoswipe'}
                                        readOnly={true}
                                        highlight={tabsHtml => highlight(tabsHtml, languages.javascript)}
                                        padding={0}
                                    />
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">  pswp </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={pswp}
                                            readOnly={true}
                                            highlight={pswp => highlight(pswp, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">html  c </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={html}
                                            readOnly={true}
                                            highlight={html => highlight(html, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> pswp</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={js}
                                            readOnly={true}
                                            highlight={js => highlight(js, languages.js)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> pswp</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={jspswp}
                                            readOnly={true}
                                            highlight={jspswp => highlight(jspswp, languages.js)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}

export default Photoswipe