import React, { Component } from 'react'

class TabLink extends Component {
    constructor(props) {
        super(props)

        this.link = React.createRef()
    }

    onClickHandler = () => {
        this.props.handleSelect(this.props.nameTab)
        this.props.handleSlider({
            width: this.link.current.offsetWidth,
            link: this.link.current.getBoundingClientRect()
        })
    }

    componentDidMount() {
        if (this.props.active) {
            this.props.handleSlider({
                width: this.link.current.offsetWidth,
                link: this.link.current.getBoundingClientRect()
            }, true)
        }
    }

    render() {
        const {
            active
        } = this.props

        if (active) {
            return (
                <div onClick={this.onClickHandler} className={this.props.className + ' active'} ref={this.link}>
                    {this.props.children}
                </div>
            )
        } else {
            return (
                <div onClick={this.onClickHandler} className={this.props.className} ref={this.link}>
                    {this.props.children}
                </div>
            )
        }
    }
}

export default TabLink