import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';
import './Spinner.scss';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const spinner = `
<span am-spinner="small"></span>
<span am-spinner></span>
<span am-spinner="large"></span>
`;

const spinner_inverted = `
<span am-spinner="small" inverted></span>
<span am-spinner inverted></span>
<span am-spinner="large" inverted></span>
`;

const spinner_center = `
<span am-spinner inverted center></span>
`;

const spinner_button = `
<button am-button="main" spinner>button
    <span am-spinner="small" inverted></span>
</button>
`;

const loader = `
<div am-loader="small">
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
</div>
<div am-loader>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
</div>
<div am-loader="large">
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
</div>
`;
const loader_center = `
<div am-loader center>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
    <div></div>
</div>
`;

export class Spinner extends Component {
    constructor(props) {
        super(props)

        this.state = {
            spinner,
            spinner_inverted,
            spinner_center,
            spinner_button,
            loader,
            loader_center
        }

        this.spinner = React.createRef()
        this.spinner_inverted = React.createRef()
        this.spinner_center = React.createRef()
        this.spinner_button = React.createRef()
        this.loader = React.createRef()
        this.loader_center = React.createRef()
    }

    componentDidMount() {
        this.spinner.current.innerHTML = this.state.spinner;
        this.spinner_inverted.current.innerHTML = this.state.spinner_inverted;
        this.spinner_center.current.innerHTML = this.state.spinner_center;
        this.spinner_button.current.innerHTML = this.state.spinner_button;
        this.loader.current.innerHTML = this.state.loader;
        this.loader_center.current.innerHTML = this.state.loader_center;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Spinner' }
                    headerText={ ' .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js (scss):</span> <CopyText language="js" copyText="import '@ui/scss/am-spinner.scss'" />  
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-spinner.scss';" />  
                    </div>
                </Header>
                <Content className="spinner">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                {/* <li><TabLink to="properties" className="tabs__link" tag="link">  </TabLink></li> */}
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                {/* <p className="example__description">       .    ,      .</p> */}
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.spinner} style={{
                                        position: 'relative',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'space-around'
                                    }}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.spinner}
                                            readOnly={true}
                                            onValueChange={spinner => { this.setState({ spinner }); this.spinner.current.innerHTML = spinner}}
                                            highlight={spinner => highlight(spinner, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">Inverted</h2>
                                    <div className="example__preview" ref={this.spinner_inverted} style={{
                                        position: 'relative',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'space-around',
                                        background: '#969696'
                                    }}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.spinner_inverted}
                                            readOnly={true}
                                            onValueChange={spinner_inverted => { this.setState({ spinner_inverted }); this.spinner_inverted.current.innerHTML = spinner_inverted}}
                                            highlight={spinner_inverted => highlight(spinner_inverted, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.spinner_center} style={{
                                        position: 'relative',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'space-around',
                                        background: '#969696',
                                        height: '100px'
                                    }}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.spinner_center}
                                            readOnly={true}
                                            onValueChange={spinner_center => { this.setState({ spinner_center }); this.spinner_center.current.innerHTML = spinner_center}}
                                            highlight={spinner_center => highlight(spinner_center, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.spinner_button} style={{
                                        position: 'relative',
                                        display: 'flex',
                                        justifyContent: 'center',
                                        alignItems: 'center',
                                    }}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.spinner_button}
                                            readOnly={true}
                                            onValueChange={spinner_button => { this.setState({ spinner_button }); this.spinner_button.current.innerHTML = spinner_button}}
                                            highlight={spinner_button => highlight(spinner_button, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.loader} style={{
                                        position: 'relative',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'space-around'
                                    }}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.loader}
                                            readOnly={true}
                                            onValueChange={loader => { this.setState({ loader }); this.loader.current.innerHTML = loader}}
                                            highlight={loader => highlight(loader, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.loader_center} style={{
                                        position: 'relative',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'space-around',
                                        height: '200px'
                                    }}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.loader_center}
                                            readOnly={true}
                                            onValueChange={loader_center => { this.setState({ loader_center }); this.loader_center.current.innerHTML = loader_center}}
                                            highlight={loader_center => highlight(loader_center, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}