import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const checkbox = `
<div am-checkbox>
    <input am-checkbox-input name="sale" type="checkbox">
    <div am-checkbox-wrapper>
        <div am-checkbox-icon></div>
        <div am-checkbox-label>  </div>
    </div>
</div>

<div am-checkbox>
    <input type="checkbox" name="sale">
    <label am-checkbox-wrapper>
        <span am-checkbox-icon></span>
        <span am-checkbox-label>  </span>
    </label>
</div>

<div am-checkbox>
    <input type="checkbox" name="sale" checked>
    <span am-checkbox-icon></span>
    <span am-checkbox-label>  </span>
</div>
`;

const checkbox_group = `
<div am-checkbox>
    <input am-checkbox-input name="sale" type="checkbox">
    <div am-checkbox-wrapper>
        <div am-checkbox-icon></div>
        <div am-checkbox-label>  </div>
    </div>
</div>

<div am-checkbox>
    <input type="checkbox" name="sale">
    <label am-checkbox-wrapper>
        <span am-checkbox-icon></span>
        <span am-checkbox-label>  </span>
    </label>
</div>

<div am-checkbox>
    <input type="checkbox" name="sale" checked>
    <span am-checkbox-icon></span>
    <span am-checkbox-label>  </span>
</div>
`;

const checkbox_disabled = `
<div am-checkbox disabled>
    <input am-checkbox-input name="sale" type="checkbox" disabled>
    <div am-checkbox-wrapper>
        <div am-checkbox-icon></div>
        <div am-checkbox-label>  </div>
    </div>
</div>

<div am-checkbox disabled>
    <input type="checkbox" name="sale" disabled>
    <label am-checkbox-wrapper>
        <span am-checkbox-icon></span>
        <span am-checkbox-label>  </span>
    </label>
</div>

<div am-checkbox disabled>
    <input type="checkbox" name="sale" checked disabled>
    <span am-checkbox-icon></span>
    <span am-checkbox-label>  </span>
</div>
`;

const scssVariables = `
// Colors
$color-main-gray-light: #E6E4E1 !default;
$color-main-blue-blur: #475077 !default;
`;

export class Checkbox extends Component {
    constructor(props) {
        super(props)

        this.state = {
            checkbox,
            checkbox_group,
            checkbox_disabled,
            scssVariables
        }

        this.checkbox = React.createRef()
        this.checkbox_group = React.createRef()
        this.checkbox_disabled = React.createRef()
    }

    componentDidMount() {
        this.checkbox.current.innerHTML = this.state.checkbox;
        this.checkbox_group.current.innerHTML = this.state.checkbox_group;
        this.checkbox_disabled.current.innerHTML = this.state.checkbox_disabled;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Checkbox' }
                    headerText={ ' .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js (scss):</span> <CopyText language="js" copyText="import '@ui/scss/am-checkbox.scss'" />
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-checkbox.scss';" />
                    </div>
                </Header>
                <Content>
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.checkbox}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.checkbox}
                                            readOnly={true}
                                            onValueChange={checkbox => { this.setState({ checkbox }); this.checkbox.current.innerHTML = checkbox}}
                                            highlight={checkbox => highlight(checkbox, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.checkbox_group}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.checkbox_group}
                                            readOnly={true}
                                            onValueChange={checkbox_group => { this.setState({ checkbox_group }); this.checkbox_group.current.innerHTML = checkbox_group}}
                                            highlight={checkbox_group => highlight(checkbox_group, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.checkbox_disabled}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.checkbox_disabled}
                                            readOnly={true}
                                            onValueChange={checkbox_disabled => { this.setState({ checkbox_disabled }); this.checkbox_disabled.current.innerHTML = checkbox_disabled}}
                                            highlight={checkbox_disabled => highlight(checkbox_disabled, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <h2 className="example__header">scss .</h2>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}