import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'

import CopyText from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext } from '../../../containers/Layout.jsx';

const create = `
const customElement = Utils.element.create('div', {'am-text': ''}, [], 'customElementCreate');

this.createRef.current.appendChild(customElement);
`;

const template = `
const customElement = Utils.element.createTemplate("<div am-text>customElementTemplate</div>");

this.templateRef.current.appendChild(customElement);
`;

const coord = `
this.coordRef.current.innerHTML = "<pre>" + JSON.stringify(Utils.element.coord(document.querySelector('body')), null, 2) + "</pre>"`;
;



export default class Element extends Component {
    static contextType = LayoutContext;

    constructor(props, context) {
        super(props, context)

        this.state = {
            create,
            template,
            coord,

            write: false,
            evalTimeout: false,
        }

        this.createRef = React.createRef()
        this.templateRef = React.createRef()
        this.coordRef = React.createRef()
    }

    componentDidMount() {
        this.context.initEval.bind(this)([
            this.state.create,
            this.state.template,
            this.state.coord
        ])
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'element' }
                    // headerText={ '    .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import Utils from '@utils'" />
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import element from '@utils/element'" />
                    </div>
                </Header>
                <Content className="Tabs">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header">create</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={`element.create(tag, attributes, children, text, ns = false)`}
                                            readOnly={true}
                                            highlight={create => highlight(create, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__preview" ref={this.createRef}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.create}
                                            readOnly={false}
                                            onValueChange={create => {
                                                this.context.evalEditorJS.bind(this)({create}, this.createRef)
                                            }}
                                            highlight={create => highlight(create, languages.javascript)}
                                            padding={0}

                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">template</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={`element.createTemplate(content)`}
                                            readOnly={true}
                                            highlight={template => highlight(template, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__preview" ref={this.templateRef}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.template}
                                            readOnly={false}
                                            onValueChange={template => this.context.evalEditorJS.bind(this)({template}, this.templateRef)}
                                            highlight={template => highlight(template, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">coord</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={`element.coord(element)`}
                                            readOnly={true}
                                            highlight={coord => highlight(coord, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__preview" ref={this.coordRef}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.coord}
                                            readOnly={false}
                                            onValueChange={coord => this.context.evalEditorJS.bind(this)({coord}, this.coordRef)}
                                            highlight={coord => highlight(coord, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}