import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';
import './Toggle.scss';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const toggle = `
<div am-toggle>
    <input am-toggle-input name="sale" type="checkbox">
    <div am-toggle-wrapper>
        <div am-toggle-track></div>
    </div>
    <div am-toggle-label>  </div>
</div>

<div am-toggle>
    <div am-toggle-label>  </div>
    <input am-toggle-input name="sale" type="checkbox">
    <div am-toggle-wrapper>
        <div am-toggle-track></div>
    </div>
</div>

<div am-toggle>
    <input am-toggle-input name="sale" type="checkbox">
    <div am-toggle-wrapper>
        <div am-toggle-track></div>
    </div>
</div>
`;

const toggle_disabled = `
<div am-toggle>
    <input am-toggle-input name="sale" type="checkbox" disabled>
    <div am-toggle-wrapper>
        <div am-toggle-track></div>
    </div>
    <div am-toggle-label>  </div>
</div>

<div am-toggle disabled>
    <div am-toggle-label>  </div>
    <input am-toggle-input name="sale" type="checkbox" disabled>
    <div am-toggle-wrapper>
        <div am-toggle-track></div>
    </div>
</div>

<div am-toggle>
    <input am-toggle-input name="sale" type="checkbox" disabled>
    <div am-toggle-wrapper>
        <div am-toggle-track></div>
    </div>
</div>
`;

const scssVariables = `
// Colors
$color-main-blue-dark: #091F7C !default;
$color-main-blue-blur: #475077 !default;
`;

export class Toggle extends Component {
    constructor(props) {
        super(props)

        this.state = {
            toggle,
            toggle_disabled,
            scssVariables
        }

        this.toggle = React.createRef()
        this.toggle_disabled = React.createRef()
    }

    componentDidMount() {
        this.toggle.current.innerHTML = this.state.toggle;
        this.toggle_disabled.current.innerHTML = this.state.toggle_disabled;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Toggle' }
                    headerText={ ' .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-toggle.scss';" />  
                    </div>        
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import '@ui/scss/am-toggle.scss'" />  
                    </div>
                </Header>
                <Content>
                    <div className="toggle">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.toggle}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.toggle}
                                            readOnly={true}
                                            onValueChange={toggle => { this.setState({ toggle }); this.toggle.current.innerHTML = toggle}}
                                            highlight={toggle => highlight(toggle, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.toggle_disabled}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.toggle_disabled}
                                            readOnly={true}
                                            onValueChange={toggle_disabled => { this.setState({ toggle_disabled }); this.toggle_disabled.current.innerHTML = toggle_disabled}}
                                            highlight={toggle_disabled => highlight(toggle_disabled, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div> 
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}