import React, { Component, Fragment }               from 'react'
import Editor                                       from 'react-simple-code-editor';
import { highlight, languages }                     from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content                                      from '../../../containers/Content.jsx'
import Header                                       from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider }     from '../../../components/ui/Tabs/'

import CopyText                                     from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext }                            from '../../../containers/Layout.jsx';

import mask                                         from '@utils/mask';

const string = `
const input = document.querySelector('input')

mask.string(input)

...

const value = mask.string(input).value
`;

const number = `
mask.number(input)
`;

const float = `
mask.float(input)
`;

const phone = `
mask.phone(input)
`;



export default class Mask extends Component {
    static contextType = LayoutContext;

    constructor(props, context) {
        super(props, context)

        this.state = {
            string,
            number,
            float,
            phone
        }

        this.stringRef = React.createRef()
        this.numberRef = React.createRef()
        this.floatRef = React.createRef()
        this.phoneRef = React.createRef()
    }

    componentDidMount() {
        mask.string(this.stringRef.current.querySelector('input'))
        mask.number(this.numberRef.current.querySelector('input'))
        mask.float(this.floatRef.current.querySelector('input'))
        mask.phone(this.phoneRef.current.querySelector('input'))
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Mask' }
                    headerText={ '    .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import mask from '@utils/mask'" />
                    </div>
                </Header>
                <Content className="Tabs">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <div className="example__about"> </div>
                                    <h2 className="example__header">  </h2>
                                    <div className="example__preview">
                                        <div am-input="string" ref={this.stringRef}>
                                            <div am-hint="top"> </div>
                                            <input type="text"/>
                                        </div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.string}
                                            readOnly={true}
                                            highlight={string => highlight(string, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">  </h2>
                                    <div className="example__preview">
                                        <div am-input="number" ref={this.numberRef}>
                                            <div am-hint="top"> </div>
                                            <input type="text"/>
                                        </div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.number}
                                            readOnly={true}
                                            highlight={number => highlight(number, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">     </h2>
                                    <div className="example__preview">
                                        <div am-input="float" ref={this.floatRef}>
                                            <div am-hint="top"> </div>
                                            <input type="text"/>
                                        </div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.float}
                                            readOnly={true}
                                            highlight={float => highlight(float, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">  </h2>
                                    <div className="example__preview">
                                        <div am-input="phone" ref={this.phoneRef}>
                                            <div am-hint="top"> </div>
                                            <input type="text" placeholder="+7 (___) ___-____"/>
                                        </div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.phone}
                                            readOnly={true}
                                            highlight={phone => highlight(phone, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}