import React, { Component, Fragment }               from 'react'
import Editor                                       from 'react-simple-code-editor';
import { highlight, languages }                     from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content                                      from '../../../containers/Content.jsx'
import Header                                       from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider }     from '../../../components/ui/Tabs/'

import CopyText                                     from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext }                            from '../../../containers/Layout.jsx';

const setAttributes = `
Element.prototype.setAttributes

element.setAttributes({
    attr1: attrValue1,
    attr2: attrValue2,
    attr3: attrValue3
})
`;

const removeAttributes = `
Element.prototype.removeAttributes

element.removeAttributes({attr1, attr2})
`;

const forEach = `
NodeList.prototype.forEach

document.querySelectorAll('div').forEach
`;



export default class Prototype extends Component {
    static contextType = LayoutContext;

    constructor(props, context) {
        super(props, context)

        this.state = {
            setAttributes,
            removeAttributes,
            forEach
        }
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Prototype' }
                    headerText={ ' .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import '@utils/prototype'" />
                    </div>
                </Header>
                <Content className="Tabs">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header">  </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.setAttributes}
                                            readOnly={true}
                                            highlight={setAttributes => highlight(setAttributes, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">  </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.removeAttributes}
                                            readOnly={true}
                                            highlight={removeAttributes => highlight(removeAttributes, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">forEach  NodeList</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.forEach}
                                            readOnly={true}
                                            highlight={forEach => highlight(forEach, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}