import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import './Accordion.scss'


class Accordion extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: false,
            collapsing: false
        }

        this.title = React.createRef()
        this.body = React.createRef()
    }

    onClickHandler(ref) {
        this.setState({ collapsing: true, active: !this.state.active })

        const body = ref.body.current
        const collapsing = setTimeout(() => {
            if (this.state.active && !this.state.collpasing) {
                this.setState({ collapsing: false })
                body.style.height = 'auto'
                clearTimeout(collapsing)
            }
        }, 100);

        if (this.state.active) {
            body.style.height = body.scrollHeight + 'px'
            setTimeout(() => {
                body.removeAttribute('style')
            }, 10);
        } else {
            body.style.height = body.scrollHeight + 'px'
        }
    }

    render() {
        const classNameTitle = 'accordion-title ' + (this.props.classNameTitle && !this.props.nav ? this.props.classNameTitle : '')
        const classNameBody = 'accordion-body ' + (this.props.classNameBody ? this.props.classNameBody : '')
        const classNameAccordion = 'accordion ' + (this.state.active ? 'active' : '')

        return (
            <div className={classNameAccordion}>
                <div
                    onClick={this.onClickHandler.bind(this, {title: this.title, body: this.body})}
                    className={classNameTitle}
                    ref={this.title}
                >
                    {this.props.nav ?
                        <NavLink to={'/frontend/utils/element'} className={'nav__link'} activeClassName={'active'} onClick={() => this.props.navHandler(true)}>
                            {this.props.title}
                        </NavLink> :
                        this.props.title
                    }

                </div>
                <div className={classNameBody} ref={this.body}>
                    {this.props.children}
                </div>
            </div>
        )
    }
}

export default Accordion