import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'
import DropdownUI from '@ui/Dropdown'

const scssVariables = `
// Dropdown
$zIndex-dropdown: 99 !default;
`;

const dropdown = `
<div am-dropdown>
    <div am-dropdown-header>dropdown</div>
    <div am-dropdown-content>
        <div am-dropdown-wrapper>
            <div am-dropdown-arrow></div>
            <ul>
                <li>dropdown-item-1</li>
                <li>dropdown-item-2</li>
                <li>dropdown-item-3</li>
                <li>dropdown-item-4</li>
                <li>dropdown-item-5</li>
            </ul>
        </div>
    </div>
</div>
`;

const dropdown_direction = `
<div am-grid="container" spacing="1">
    <div am-grid="item" elg="3" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown-bottom (default)</div>
            <div am-dropdown-content direction="bottom">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow></div>
                    <ul>
                        <li>
                            <a href="/frontend/components/dropdown">/frontend/components/dropdown</a>
                        </li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="3" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown-top</div>
            <div am-dropdown-content direction="top">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow></div>
                    <ul>
                        <li>dropdown-item-1</li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</div>
`;

const dropdown_align = `
<div am-grid="container" spacing="1">
    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown align-left</div>
            <div am-dropdown-content align="left">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow align="left"></div>
                    <ul>
                        <li>
                            <a href="/uikit/components/dropdown">/uikit/components/dropdown</a>
                        </li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown align-stretch</div>
            <div am-dropdown-content align="stretch">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow align="center"></div>
                    <ul>
                        <li>dropdown-item-1</li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown align-right</div>
            <div am-dropdown-content align="right">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow align="right"></div>
                    <ul>
                        <li>dropdown-item-1</li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown align-right</div>
            <div am-dropdown-content align="left" direction="top">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow align="left"></div>
                    <ul>
                        <li>dropdown-item-1</li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown align-right</div>
            <div am-dropdown-content align="stretch" direction="top">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow align="center"></div>
                    <ul>
                        <li>dropdown-item-1</li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

    <div am-grid="item" elg="4" sm="12" spacing="1">
        <div am-dropdown>
            <div am-dropdown-header>dropdown align-right</div>
            <div am-dropdown-content align="right" direction="top">
                <div am-dropdown-wrapper>
                    <div am-dropdown-arrow align="right"></div>
                    <ul>
                        <li>dropdown-item-1</li>
                        <li>dropdown-item-2</li>
                        <li>dropdown-item-3</li>
                        <li>dropdown-item-4</li>
                        <li>dropdown-item-5</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</div>
`;

export class Dropdown extends Component {
    constructor(props) {
        super(props)

        this.state = {
            dropdown,
            dropdown_direction,
            dropdown_align,
            scssVariables
        }

        this.dropdown = React.createRef()
        this.dropdown_direction = React.createRef()
        this.dropdown_align = React.createRef()
    }

    componentDidMount() {
        DropdownUI.bubbleInit()

        this.dropdown.current.innerHTML = this.state.dropdown;
        this.dropdown_direction.current.innerHTML = this.state.dropdown_direction;
        this.dropdown_align.current.innerHTML = this.state.dropdown_align;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Dropdown' }
                    headerText={ '  .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import Dropdown from '@ui/Dropdown'" />  
                    </div>   
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js (scss):</span> <CopyText language="js" copyText="import '@ui/scss/am-dropdown.scss'" />  
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-dropdown.scss';" />  
                    </div>
                </Header>
                <Content className="dropdown">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <li><TabLink to="properties" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.dropdown}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.dropdown}
                                            readOnly={true}
                                            onValueChange={dropdown => { this.setState({ dropdown }); this.dropdown.current.innerHTML = dropdown}}
                                            highlight={dropdown => highlight(dropdown, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <p className="example__description">   ,   <span className="token attr-name"> direction</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>bottom|top<span className="token punctuation">" </span></span>
                                      <span className="token attr-name">am-dropdown-content</span>
                                    </p>
                                    <div className="example__preview" ref={this.dropdown_direction}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.dropdown_direction}
                                            readOnly={true}
                                            onValueChange={dropdown_direction => { this.setState({ dropdown_direction }); this.dropdown_direction.current.innerHTML = dropdown_direction}}
                                            highlight={dropdown_direction => highlight(dropdown_direction, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <p className="example__description">   ,   <span className="token attr-name"> align</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>left|stretch|right<span className="token punctuation">" </span></span>
                                      <span className="token attr-name">am-dropdown-content</span>
                                    </p>
                                    <div className="example__preview" ref={this.dropdown_align}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.dropdown_align}
                                            readOnly={true}
                                            onValueChange={dropdown_align => { this.setState({ dropdown_align }); this.dropdown_align.current.innerHTML = dropdown_align}}
                                            highlight={dropdown_align => highlight(dropdown_align, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div>
                            </TabContent>
                            <TabContent for="properties" className="tabs__content" tag="content">
                                <div am-table-responsive={''}>
                                    <table am-table={''} border-bottom={''}>
                                        <caption>
                                           dropdown-content
                                        </caption>
                                        <thead>
                                            <tr>
                                                <th></th>
                                                <th>  </th>
                                                <th></th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>direction</td>
                                                <td>bottom</td>
                                                <td>top&nbsp;| bottom&nbsp;|</td>
                                                <td></td>
                                            </tr>
                                            <tr>
                                                <td>align</td>
                                                <td>center</td>
                                                <td>left&nbsp;| center&nbsp;| stretch&nbsp;| right&nbsp;|</td>
                                                <td></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}