import React, { Component, Fragment }               from 'react'
import Editor                                       from 'react-simple-code-editor';
import { highlight, languages }                     from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content                                      from '../../../containers/Content.jsx'
import Header                                       from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider }     from '../../../components/ui/Tabs/'

import CopyText                                     from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext }                            from '../../../containers/Layout.jsx';

const is = `
/**
 * @param {any} o
 */
object.is(o)
`;

const extend = `
/**
 * Object.assign
 * @param {any} target
 */
object.extend(target, ...args)
`;


export default class ObjectPage extends Component {
    static contextType = LayoutContext;

    constructor(props, context) {
        super(props, context)

        this.state = {
            is,
            extend
        }
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Object' }
                    headerText={ '    .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import object from '@utils/object'" />
                    </div>
                </Header>
                <Content className="Tabs">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header">   </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.is}
                                            readOnly={true}
                                            highlight={is => highlight(is, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">  </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.extend}
                                            readOnly={true}
                                            highlight={extend => highlight(extend, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}