import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const grid_x4 = `
<div am-container="grid">
    <div am-grid="container" spacing="2">
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">1 </div>
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">2 </div>
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">3 </div>
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">4 </div>
    </div>

    <div am-grid="container" spacing="2">
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">1 </div>
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">2 </div>
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">3 </div>
        <div am-grid="item" elg="3" md="6" sm="12" spacing="1">4 </div>
    </div>
</div>
`;

const gridAlign = `
<div am-container="grid">
    <div am-grid="container" spacing="1" justify="center" align-items="start">
        <div am-grid="item" elg="3" spacing="1">1 </div>
        <div am-grid="item" elg="3" spacing="1">2 </div>
        <div am-grid="item" elg="3" spacing="1">3 </div>
    </div>
    <div am-grid="container" spacing="1" justify="center" align-items="center">
        <div am-grid="item" elg="3" spacing="1">1 </div>
        <div am-grid="item" elg="3" spacing="1">2 </div>
        <div am-grid="item" elg="3" spacing="1">3 </div>
    </div>
    <div am-grid="container" spacing="1" justify="center" align-items="end">
        <div am-grid="item" elg="3" spacing="1">1 </div>
        <div am-grid="item" elg="3" spacing="1">2 </div>
        <div am-grid="item" elg="3" spacing="1">3 </div>
    </div>
    <div am-grid="container" spacing="1" justify="start" align-items="center">
        <div am-grid="item" elg="3" spacing="1">1 </div>
        <div am-grid="item" elg="3" spacing="1">2 </div>
        <div am-grid="item" elg="3" spacing="1">3 </div>
    </div>
    <div am-grid="container" spacing="1" justify="center" align-items="center">
        <div am-grid="item" elg="3" spacing="1">1 </div>
        <div am-grid="item" elg="3" spacing="1">2 </div>
        <div am-grid="item" elg="3" spacing="1">3 </div>
    </div>
    <div am-grid="container" spacing="1" justify="end" align-items="center">
        <div am-grid="item" elg="3" spacing="1">1 </div>
        <div am-grid="item" elg="3" spacing="1">2 </div>
        <div am-grid="item" elg="3" spacing="1">3 </div>
    </div>
</div>
`;

const gridSize = `
<div am-container="grid">
    <div am-grid="container" spacing="2">
        <div am-grid="item" elg="2" lg="4" md="6" sm="12" spacing="2">1 </div>
        <div am-grid="item" elg="2" lg="4" md="6" sm="12" spacing="2">2 </div>
        <div am-grid="item" elg="2" lg="4" md="6" sm="12" spacing="2">3 </div>
        <div am-grid="item" elg="2" lg="4" md="6" sm="12" spacing="2">4 </div>
        <div am-grid="item" elg="2" lg="4" md="6" sm="12" spacing="2">5 </div>
        <div am-grid="item" elg="2" lg="4" md="6" sm="12" spacing="2">6 </div>
    </div>
</div>
`;

const container = `
<div am-wrapper="contentName">
    <div am-container="contentName">
        content
    </div>
</div>
`;

const scssVariables = `
// Container
$container-width: 1170px;
$container-width-lg: 960px;
$container-width-md: 768px;
$container-width-sm: 100%;

// Grid
$columns: 12;
$offset: 30px;

$offset-lg: 30px;
$offset-md: 30px;
$offset-sm: 10px;

// Breakpoints
$lg: 1169px;
$md: 959px;
$sm: 767px;
`;

export class Grid extends Component {
    constructor(props) {
        super(props)

        this.state = {
            grid_x4,
            gridSize,
            gridAlign,
            container,
            scssVariables
        }

        this.grid_x4 = React.createRef()
        this.gridSize = React.createRef()
        this.gridAlign = React.createRef()
    }

    componentDidMount() {
        this.grid_x4.current.innerHTML = this.state.grid_x4;
        this.gridSize.current.innerHTML = this.state.gridSize;
        this.gridAlign.current.innerHTML = this.state.gridAlign;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ '' }
                    headerText={ '    ,  ,       .' }
                >          
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-grid.scss';" />  
                    </div>        
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import '@ui/scss/am-modal.scss'" />  
                    </div>                            
                </Header>
                <Content className="grid">
                    <div className="example">
                        <Tabs /*selectedTab="examples"*/ className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <li><TabLink to="properties" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"> .</h2>
                                    <p className="example__about">
                                            
                                        <span className="token attr-name"> am-grid</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>item<span className="token punctuation">"</span></span>.       -
                                        <span className="token attr-name"> am-grid</span><span className="token attr-value"><span className="token punctuation">=</span><span className="token punctuation">"</span>container<span className="token punctuation">"</span></span>.<span className="token attr-name">&nbsp;spacing </span> ( 1  4)       .
                                    </p>
                                    <p className="example__about">
                                            4 
                                    </p>
                                    <div className="example__preview" ref={this.grid_x4}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.grid_x4}
                                            onValueChange={grid_x4 => {
                                                this.setState({ grid_x4});
                                                this.grid_x4.current.innerHTML = grid_x4
                                            }}
                                            readOnly={true}
                                            highlight={grid_x4 => highlight(grid_x4, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                    <h2 className="example__header">   .</h2>
                                    <ul className="example__list">
                                        <li>   - direction</li>
                                        <li>    - justify</li>
                                        <li>    - align-items</li>
                                        <li>  - align-content</li>
                                    </ul>
                                    <div className="example__preview gridAlign" ref={this.gridAlign}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.gridAlign}
                                            onValueChange={gridAlign => {
                                                this.setState({ gridAlign});
                                                this.gridAlign.current.innerHTML = gridAlign
                                            }}
                                            readOnly={true}
                                            highlight={gridAlign => highlight(gridAlign, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                    <h2 className="example__header">.</h2>
                                    {/* <p className="example__about">
                                             100% ,     ,                   :
                                    </p> */}
                                    <ul className="example__list">
                                        <li>elg -     </li>
                                        <li>lg -   &le; <span className="token attr-value"> 1169px</span></li>
                                        <li>md -   &le;<span className="token attr-value"> 959px</span></li>
                                        <li>sm -   &le;  <span className="token attr-value"> 767px</span></li>
                                        <li>  12</li>
                                    </ul>
                                    <div className="example__preview" ref={this.gridSize}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.gridSize}
                                            onValueChange={gridSize => {
                                                this.setState({ gridSize});
                                                this.gridSize.current.innerHTML = gridSize
                                            }}
                                            readOnly={true}
                                            highlight={gridSize => highlight(gridSize, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                    <h2 className="example__header">.</h2>
                                    <p className="example__about">
                                          
                                        <span className="token attr-name"> am-container</span>,         <span className="token attr-name"> am-container-fluid</span>.
                                    </p>
                                    <div className="example__code">
                                        <Editor
                                                value={this.state.container}
                                                onValueChange={container => {
                                                    this.setState({ container });
                                                }}
                                                readOnly={true}
                                                highlight={container => highlight(container, languages.markup)}
                                                padding={0}
                                            />
                                    </div>
                                    
                                    <h2 className="example__header">scss .</h2>
                                    <p className="example__about">                                        
                                        <div style={{display: 'flex', alignItems: 'center'}}>
                                            <span style={{marginTop: '9px', marginRight: '10px'}}> import' scss ,   :</span>  
                                        </div>   
                                    </p>
                                    <div className="example__code">
                                        <Editor
                                                value={`@import './yourVariablesComponent.scss'; 
@import 'node_modules/pobeda-ui/src/ui/scss/am-grid.scss';`}
                                                onValueChange={scssVariables => {
                                                    this.setState({ scssVariables });
                                                }}
                                                readOnly={true}
                                                highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                                padding={0}
                                            />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                                value={this.state.scssVariables}
                                                onValueChange={scssVariables => {
                                                    this.setState({ scssVariables });
                                                }}
                                                readOnly={true}
                                                highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                                padding={0}
                                            />
                                    </div>
                                </div>
                            </TabContent>
                            <TabContent for="properties" className="tabs__content" tag="content">
                                <div am-table-responsive={''}>
                                    <table am-table={''} border-bottom={''}>
                                        <caption>
                                            Grid container
                                        </caption>
                                        <thead>
                                            <tr>
                                                <th></th>
                                                <th>  </th>
                                                <th></th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>direction</td>
                                                <td>row</td>
                                                <td>row&nbsp;| row-reverse&nbsp;| column&nbsp;| column-reverse&nbsp;|</td>
                                                <td>  </td>
                                            </tr>
                                            <tr>
                                                <td>justify</td>
                                                <td>start</td>
                                                <td>start&nbsp;| end&nbsp;| center&nbsp;| space-between&nbsp;| space-around&nbsp;|</td>
                                                <td>   </td>
                                            </tr>
                                            <tr>
                                                <td>align-items</td>
                                                <td>stretch</td>
                                                <td>start&nbsp;| end&nbsp;| center&nbsp;| baseline&nbsp;| stretch&nbsp;|</td>
                                                <td>   </td>
                                            </tr>
                                            <tr>
                                                <td>align-content</td>
                                                <td></td>
                                                <td>start&nbsp;| end&nbsp;| center&nbsp;| space-between&nbsp;| space-around&nbsp;| stretch&nbsp;|</td>
                                                <td> </td>
                                            </tr>
                                            <tr>
                                                <td>spacing</td>
                                                <td></td>
                                                <td>1&nbsp;| 2&nbsp;| 3&nbsp;| 4&nbsp;|</td>
                                                <td>  </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div am-table-responsive={''}>
                                    <table am-table={''} border-bottom={''}>
                                        <caption>
                                            Grid item
                                        </caption>
                                        <thead>
                                            <tr>
                                                <th></th>
                                                <th>  </th>
                                                <th></th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>align-self</td>
                                                <td>start</td>
                                                <td>start&nbsp;| end&nbsp;| center&nbsp;| baseline&nbsp;| stretch&nbsp;|</td>
                                                <td> </td>
                                            </tr>
                                            <tr>
                                                <td>spacing</td>
                                                <td></td>
                                                <td>1&nbsp;| 2&nbsp;| 3&nbsp;| 4&nbsp;|</td>
                                                <td>  </td>
                                            </tr>
                                            <tr>
                                                <td>elg</td>
                                                <td></td>
                                                <td>1-12</td>
                                                <td>      </td>
                                            </tr>
                                            <tr>
                                                <td>lg</td>
                                                <td></td>
                                                <td>1-12</td>
                                                <td>     &le; 1169px</td>
                                            </tr>
                                            <tr>
                                                <td>md</td>
                                                <td></td>
                                                <td>1-12</td>
                                                <td>     &le; 959px</td>
                                            </tr>
                                            <tr>
                                                <td>sm</td>
                                                <td></td>
                                                <td>1-12</td>
                                                <td>     &le; 767px</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}