import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';
import './Form.scss';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
// import utils from '@utils';
import SelectUI from '@ui/Select';
import CopyText from '../../../components/CopyText/CopyText.jsx';

const form_field = `
<form am-form="news">
    <div am-form-field width="100">
        <div am-input="fio">
            <div am-hint="top"></div>
            <div am-input-group>
                <div am-check></div>
                <input name="fio" type="text" placeholder="" autocomplete="off">
            </div>
        </div>

        <div am-input="name">
            <div am-hint="top"> <span am-required="">*</span></div>
            <div am-input-group>
                <div am-check></div>
                <input name="name" type="text" placeholder="" autocomplete="off">
            </div>
        </div>

        <div am-input="patronymic">
            <div am-hint="top"></div>
            <div am-input-group>
                <div am-check></div>
                <input name="patronymic" type="text" placeholder="" autocomplete="off">
            </div>
        </div>
    </div>

    <div am-form-field width="auto">
        <div am-input="phone">
            <div am-hint="top"> <span am-required="">*</span></div>
            <div am-input-group>
                <div am-check></div>
                <input name="phone" placeholder="+7 (900) 000-0000" type="text" autocomplete="off">
            </div>
        </div>

        <div am-input="email">
            <div am-hint="top">Email</div>
            <div am-input-group>
                <div am-check></div>
                <input name="email" type="text" placeholder="" autocomplete="off">
            </div>
        </div>
    </div>

    <div am-form-field width="auto">
        <div am-select am-input="city">
            <input am-select-input type="hidden" name="city" value="0">
            <div am-select-value=""> </div>
            <div am-select-wrapper>
                <div am-select-options>
                    <div am-select-option="0"> </div>
                    <div am-select-option="1"></div>
                    <div am-select-option="2"></div>
                    <div am-select-option="3"></div>
                    <div am-select-option="4"></div>
                    <div am-select-option="5"></div>
                </div>
            </div>
        </div>
    </div>

    <div am-form-field>
        <div am-checkbox>
            <input am-checkbox-input name="subscribe" type="checkbox">
            <div am-checkbox-wrapper>
                <div am-checkbox-icon></div>
                <div am-checkbox-label>  </div>
            </div>
        </div>
    </div>

    <div am-form-field>
        <button am-button="main" type="submit"></button>
    </div>
</form>
`;

export class Form extends Component {
    constructor(props) {
        super(props)

        this.state = {
            form_field,
        }

        this.form_field = React.createRef()
    }

    componentDidMount() {
        this.form_field.current.innerHTML = this.state.form_field;

        new SelectUI(this.form_field.current.querySelector('[am-select]'), {
            placeholder: ' ',
            placeholderOption: ' '
        });
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Form' }
                    headerText={ '  (am-form-field).' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-form.scss';" />  
                    </div>        
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js (scss):</span> <CopyText language="js" copyText="import '@ui/scss/am-form.scss'" />  
                    </div>
                </Header>
                <Content className="form">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <div className="example__preview" ref={this.form_field}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.form_field}
                                            readOnly={true}
                                            onValueChange={form_field => { this.setState({ form_field }); this.form_field.current.innerHTML = form_field }}
                                            highlight={form_field => highlight(form_field, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}