import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'

import SelectUI from '@ui/Select';

import CopyText from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext } from '../../../containers/Layout.jsx';

const getField = `
/**
 *      
 * @param {HTMLElement} element
 * @returns {object}    {name, input, container, value}
 */
utils.form.getField(element)
`;

const getField_js = `
const inputContainer = document.querySelector('[am-input="name"]')

const inputfield = Utils.form.getField(inputContainer)
`;

const getFields = `
/**
 *      
 * @param {HTMLElement} form
 * @returns {object}     [...{name, input, container, value}]
 */
utils.form.getFields(form)
`;

const getFields_js = `
const form = document.querySelector('form')

const inputfields = utils.form.getFields(form)
`;

const validate = `
/**
 *   
 * @param {object} element -     {name, input, container, value}
 * @param {object} options -   {empty, text, number, phone, email}
 * @param {object} result - ,    {name: true|false}     *
 * @returns {object}    {name: true|false}
 */
utils.form.validate(element, options = {}, result = {})
`;

const validate_js = `
const inputContainer = document.querySelector('[am-input="name"]')
const inputfield = Utils.form.getField(inputContainer)

const form = document.querySelector('form')

const validateOptions = {
    empty: false
}

let errors = {}

utils.form.validate(inputfield, validateOptions(inputfield), errors)

utils.form.getFields(form).forEach(element => utils.form.validate(element, validateOptions(element), errors))
`;


const handlerChange = `
/**
 *   ,   
 * @param {HTMLElement} element  
 * @param {object} options ,      {empty, text, number, phone, email}
 */
utils.form.handlerChange(element, options = {})
`;

const handlerChange_js = `
const fields = window.Utils.form.getFields(form)
const button = form.querySelector('button')

const validateOptions = element => {
    return {
        empty: true,
        text: element.name === 'name' ? true : false,
        email: element.name === 'email' ? true : false,
        phone: element.name === 'phone' ? true : false
    }
}

let errors = {}

fields.forEach(element => window.Utils.form.handlerChange(element.container, validateOptions(element)))

button.addEventListener('click', e => {
    e.preventDefault()

    window.Utils.form.getFields(form).forEach(element => window.Utils.form.validate(element, validateOptions(element), errors))
    
    if (window.Utils.form.checkError(errors)) {
        console.log(errors);
        return
    } else {
        window.Utils.form.getFields(form).forEach(element => {
            window.Utils.form.clear(element.input)
        })
    }
})
`;

const form = `
<form am-form="news">
    <div am-form-field width="100">
        <div am-input="fio">
            <div am-hint="top"></div>
            <div am-input-group>
                <div am-check></div>
                <input name="fio" type="text" placeholder="" autocomplete="off">
            </div>
        </div>

        <div am-input="name">
            <div am-hint="top"> <span am-required="">*</span></div>
            <div am-input-group>
                <div am-check></div>
                <input name="name" type="text" placeholder="" autocomplete="off">
            </div>
        </div>

        <div am-input="patronymic">
            <div am-hint="top"></div>
            <div am-input-group>
                <div am-check></div>
                <input name="patronymic" type="text" placeholder="" autocomplete="off">
            </div>
        </div>
    </div>

    <div am-form-field width="auto">
        <div am-input="phone">
            <div am-hint="top"> <span am-required="">*</span></div>
            <div am-input-group>
                <div am-check></div>
                <input name="phone" placeholder="+7 (900) 000-0000" type="text" autocomplete="off">
            </div>
        </div>

        <div am-input="email">
            <div am-hint="top">Email</div>
            <div am-input-group>
                <div am-check></div>
                <input name="email" type="text" placeholder="" autocomplete="off">
            </div>
        </div>
    </div>

    <div am-form-field width="auto">
        <div am-select am-input="city">
            <input am-select-input type="hidden" name="city" value="0">
            <div am-select-value=""> </div>
            <div am-select-wrapper>
                <div am-select-options>
                    <div am-select-option="0"> </div>
                    <div am-select-option="1"></div>
                    <div am-select-option="2"></div>
                    <div am-select-option="3"></div>
                    <div am-select-option="4"></div>
                    <div am-select-option="5"></div>
                </div>
            </div>
        </div>
    </div>

    <div am-form-field>
        <div am-checkbox>
            <input am-checkbox-input name="subscribe" type="checkbox">
            <div am-checkbox-wrapper>
                <div am-checkbox-icon></div>
                <div am-checkbox-label>  </div>
            </div>
        </div>
    </div>

    <div am-form-field>
        <button am-button="main"></button>
    </div>
</form>
`;



export default class Form extends Component {
    static contextType = LayoutContext;

    constructor(props, context) {
        super(props, context)

        this.state = {}

        this.formRef = React.createRef()
    }

    componentDidMount() { 
        this.formRef.current.innerHTML = form

        this.formValidate(this.formRef.current)

        const customSelect = new SelectUI(this.formRef.current.querySelector('[am-select]'), {
            name: 'selectCreate',
            options: [
                {value: 0, label: ' '},
                {value: 1, label: ''},
                {value: 2, label: ''},
                {value: 3, label: ''},
                {value: 4, label: ''},
                {value: 5, label: ''},
            ],
            placeholder: ' ',
            placeholderOption: ' '
        });
    } 
    
    formValidate(form) {
        const fields = window.Utils.form.getFields(form)
        const button = form.querySelector('button')

        const validateOptions = element => {
            return {
                empty: true,
                text: element.name === 'name' ? true : false,
                email: element.name === 'email' ? true : false,
                phone: element.name === 'phone' ? true : false
            }
        }
        
        let errors = {}
        
        fields.forEach(element => window.Utils.form.handlerChange(element.container, validateOptions(element)))
        
        button.addEventListener('click', e => {
            e.preventDefault()

            window.Utils.form.getFields(form).forEach(element => window.Utils.form.validate(element, validateOptions(element), errors))
            
            if (window.Utils.form.checkError(errors)) {
                console.log(errors);
                return
            } else {
                window.Utils.form.getFields(form).forEach(element => {
                    window.Utils.form.clear(element.input)
                })
            }
        })
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'form' }
                    headerText={ '    .' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import Utils from '@utils'" />
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <CopyText language="js" copyText="import form from '@utils/form'" />
                    </div>
                </Header>
                <Content className="Tabs">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header">getField</h2>
                                    <p className="example__about">   form.validate</p>
                                    <div className="example__code">
                                        <Editor
                                            value={getField}
                                            readOnly={true}
                                            highlight={getField => highlight(getField, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={getField_js}
                                            readOnly={true}
                                            highlight={getField => highlight(getField, languages.javascript)}
                                            padding={0}                                            
                                        />
                                    </div>
                                </div>    
                                <div am-content="example">
                                    <h2 className="example__header">getFields</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={getFields}
                                            readOnly={true}
                                            highlight={getField => highlight(getField, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={getFields_js}
                                            readOnly={true}
                                            highlight={getField => highlight(getField, languages.javascript)}
                                            padding={0}                                            
                                        />
                                    </div>
                                </div>    
                                <div am-content="example">
                                    <h2 className="example__header">validate</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={validate}
                                            readOnly={true}
                                            highlight={validate => highlight(validate, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={validate_js}
                                            readOnly={true}
                                            highlight={validate => highlight(validate, languages.javascript)}
                                            padding={0}                                            
                                        />
                                    </div>
                                </div>    
                                <div am-content="example">
                                    <h2 className="example__header">handlerChange</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={handlerChange}
                                            readOnly={true}
                                            highlight={validate => highlight(validate, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={handlerChange_js}
                                            readOnly={true}
                                            highlight={validate => highlight(validate, languages.javascript)}
                                            padding={0}                                            
                                        />
                                    </div>
                                    <div className="example__preview" ref={this.formRef}></div>
                                    {/* <div className="example__code">
                                        <Editor
                                            value={form_field}
                                            readOnly={true}
                                            // onValueChange={form_field => { this.setState({ form_field }); this.form_field.current.innerHTML = form_field }}
                                            highlight={form_field => highlight(form_field, languages.markup)}
                                            padding={0}
                                        />
                                    </div> */}
                                </div>                        
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}