import React, { Component, Fragment } from 'react'

import { Element, EventPage, Form, EventEmitterPage, Mask, ObjectPage, Prototype } from './routeUtils'

class UtilsView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            views: {
                element: <Element/>,
                event: <EventPage/>,
                form: <Form/>,
                EventEmitter: <EventEmitterPage/>,
                mask: <Mask/>,
                object: <ObjectPage/>,
                prototype: <Prototype/>
            },
            renderView: false
        }
    }

    componentDidMount() {
        const view = this.state.views[this.props.match.params.name]

        if (!view) {
            this.props.history.push('/')
        } else {
            this.setState({
                renderView: true
            })
        }
    }

    renderView = () => {
        const name = this.props.match.params.name
        const views = {...this.state.views}

        for(let view in views) {
            if (name === view) {
                return (
                    views[view]
                )
            }
        }
    }

    render() {

        return (
            <Fragment>
                { this.state.renderView && this.renderView() }
            </Fragment>
        )
    }
}

export default UtilsView