import React, { Component, Fragment } from 'react'

import { Button, Input, Checkbox, Radio, Tooltip, Dropdown, Modal, Form, Toggle, Select, Progress, Spinner, Accordion, TabsPage} from './routeComponent.jsx'

class ComponentView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            views: {
                button: <Button/>,
                input: <Input/>,
                checkbox: <Checkbox/>,
                radio: <Radio/>,
                tooltip: <Tooltip/>,
                dropdown: <Dropdown/>,
                modal: <Modal/>,
                form: <Form/>,
                toggle: <Toggle/>,
                select: <Select/>,
                progress: <Progress/>,
                spinner: <Spinner/>,
                accordion: <Accordion/>,
                tabs: <TabsPage/>
            },
            renderView: false
        }
    }

    componentDidMount() {
        const view = this.state.views[this.props.match.params.name]

        if (!view) {
            this.props.history.push('/')
        } else {
            this.setState({
                renderView: true
            })
        }
    }

    renderView = () => {
        const name = this.props.match.params.name
        const views = {...this.state.views}

        for(let view in views) {
            if (name === view) {
                return (
                    views[view]
                )
            }
        }

        //this.props.history.push('/')
    }

    render() {

        return (
            <Fragment>
                { this.state.renderView && this.renderView() }
            </Fragment>
        )
    }
}

export default ComponentView