import React, { Component } from 'react'
import './Nav.scss'
import {
    NavLink,
    matchPath
} from 'react-router-dom'
import SimpleBar from 'simplebar-react'
import 'simplebar/dist/simplebar.min.css';

import Accordion from '../ui/Accordion/Accordion.jsx'
import BurgerButton from '../ui/BurgerButton/BurgerButton.jsx'

class Nav extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: true
        }
    }

    burgerButtonHandler = (state) => {
        this.setState({active: state})
    }

    render() {
        const cls = ['nav']
        const activeClassName = !!matchPath(window.location.pathname, { path: "/pobeda-ui", exact: true }) ? "active" : ""

        if (this.state.active) {
            cls.push('active')
        }

        return (
            <nav className={cls.join(' ')}>
                <BurgerButton active={this.state.active} click={this.burgerButtonHandler}/>
                <SimpleBar style={{ maxHeight: '100%' }} autoHide={false}>
                    <ul className={'nav__inner'}>
                        <li className={'nav__item'}>
                            <NavLink to={'/'} exact={true} className={'nav__link ' + activeClassName}>
                                
                            </NavLink>
                        </li>
                        <li className={'nav__item'}>
                                <Accordion title={''} classNameTitle={'nav__link'}>
                                    <ul>
                                        <li>
                                            <NavLink to={'/pobeda-ui/markup/grid'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                                
                                            </NavLink>
                                        </li>
                                        {/* <li>
                                            <NavLink to={'/pobeda-ui/markup/colors'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                                
                                            </NavLink>
                                        </li> */}
                                        {/* <li>
                                            <NavLink to={'/pobeda-ui/markup/typography'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                                
                                            </NavLink>
                                        </li> */}
                                        {/* <li>
                                            <NavLink to={'/pobeda-ui/markup/icons'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                                
                                            </NavLink>
                                        </li> */}
                                    </ul>
                                </Accordion>
                        </li>
                        <li className={'nav__item'}>
                            <Accordion title={''} classNameTitle={'nav__link'}>
                                <ul>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/button'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    {/* <li>
                                        <NavLink to={'/pobeda-ui/components/link'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li> */}
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/input'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                             
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/checkbox'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/radio'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/toggle'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/select'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/form'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/modal'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                             
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/dropdown'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                             
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/tooltip'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                             
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/progress'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/spinner'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    {/* <li>
                                        <NavLink to={'/pobeda-ui/components/tooltip'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li> */}
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/accordion'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/components/tabs'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            
                                        </NavLink>
                                    </li>
                                </ul>
                            </Accordion>
                        </li>

                        {/* <li className={'nav__item'}>
                            <Accordion title={''} classNameTitle={'nav__link'}>
                                <ul>
                                    <li>
                                        <NavLink to={'/pobeda-ui/methods/numberformat'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            numberFormat
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/methods/declension'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            declension
                                        </NavLink>
                                    </li>
                                </ul>
                            </Accordion>
                        </li> */}

                        <li className={'nav__item'}>
                            <Accordion title={''} classNameTitle={'nav__link'}>
                                <ul>
                                    <li>
                                        <NavLink to={'/pobeda-ui/plugins/swiper'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            Swiper ()
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/plugins/simplebar'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            SimpleBar ()
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/plugins/photoswipe'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            PhotoSwipe ()
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/plugins/nouislider'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            noUiSldier ()
                                        </NavLink>
                                    </li>
                                </ul>
                            </Accordion>
                        </li>
                        <li className={'nav__item'}>
                            <Accordion title={''} classNameTitle={'nav__link'}>
                                <ul>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/element'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            element
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/event'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            event
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/form'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            form
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/EventEmitter'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            EventEmitter
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/mask'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            mask
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/object'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            object
                                        </NavLink>
                                    </li>
                                    <li>
                                        <NavLink to={'/pobeda-ui/utils/prototype'} className={'nav__link'} activeClassName={'active'} onClick={() => this.burgerButtonHandler(true)}>
                                            prototype
                                        </NavLink>
                                    </li>
                                </ul>
                            </Accordion>
                        </li>
                        <li className={'nav__item'}>
                            <a href="/pobeda-ui/uikit.html" className={'nav__link'}>Uikit</a>
                        </li>
                    </ul>
                </SimpleBar>
                {/* <div className={'nav__logo'}></div> */}
            </nav>
        )
    }
}

export default Nav