import React, { Component, Fragment }               from 'react'
import Editor                                       from 'react-simple-code-editor';
import { highlight, languages }                     from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content                                      from '../../../containers/Content.jsx'
import Header                                       from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider }     from '../../../components/ui/Tabs/'

import CopyText                                     from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext }                            from '../../../containers/Layout.jsx';

const tabs = `
const tabsSwiper = new Swiper(document.querySelector('.swiper-container'), {
    spaceBetween: 10,
    loop: false,
    slidesPerView: 'auto',
})
`;

const tabsHtml = `
<div class="swiper-container">
    <div am-tab-list class="swiper-wrapper">
        <div am-tab="novelty" data-tab="novelty" class="swiper-slide">
            novelty.title
        </div>
    </div>
</div>
`;

const cards = `
const noveltiesSwiper = new Swiper(document.querySelector('.swiper-container'), {
    // wrapperClass: 'swiper-wrapper',
    // slideClass: 'swiper-slide',
    direction: 'horizontal',
    loop: false,
    spaceBetween: 10,
    init: false,
    observer: true,
    speed: 1000,
    autoplay: {
        delay: 3000,
    },
    updateOnWindowResize: true,
    observeSlideChildren: true,
    navigation: {
        nextEl: item.parentNode.querySelector('.swiper-button-next'),
        prevEl: item.parentNode.querySelector('.swiper-button-prev')
    },
    on: {
        init: function() {
            console.log('init');
        },
    },
    breakpoints: {
        1: {
            slidesPerView: 'auto',
            spaceBetween: 10,
            slidesOffsetBefore: 0,
            slidesOffsetAfter: 0,
        },
        550: {
            slidesPerView: 3,
            spaceBetween: 10,
            slidesOffsetBefore: 0,
            slidesOffsetAfter: 0,
        },
        767: {
            slidesPerView: 4,
            spaceBetween: 10,
            slidesOffsetBefore: 0,
            slidesOffsetAfter: 0,
        },
        1170: {
            slidesPerView: 5,
            spaceBetween: 10,
            slidesOffsetBefore: 0,
            slidesOffsetAfter: 0,
        },
    }
});

noveltiesSwiper.init();

window.addEventListener('resize', () => {
    noveltiesSwiper.update();
})
`;


export default class SwiperPage extends Component {
    static contextType = LayoutContext;

    constructor(props, context) {
        super(props, context)

        this.state = {
            tabs,
            cards
        }
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Swiper' }
                    headerText={ '.' }
                >
                <div style={{display: 'flex', flexDirection: 'column'}}>
                        <CopyText language="js" copyText="import Swiper from 'swiper'" />
                        <CopyText language="scss" copyText="@import 'node_modules/swiper/swiper.scss';" />
                    </div>
                </Header>
                <Content className="Tabs">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <p className="example__about"> <a target="_blank"  rel="noreferrer" href="https://swiperjs.com/"></a></p>
                                <div className="example__code">
                                    <Editor
                                        value={'npm i swiper'}
                                        readOnly={true}
                                        highlight={tabsHtml => highlight(tabsHtml, languages.javascript)}
                                        padding={0}
                                    />
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={tabsHtml}
                                            readOnly={true}
                                            highlight={tabsHtml => highlight(tabsHtml, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.tabs}
                                            readOnly={true}
                                            highlight={tabs => highlight(tabs, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">  </h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.cards}
                                            readOnly={true}
                                            highlight={cards => highlight(cards, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}