import React, { Component } from 'react'
import './CopyText.scss'

import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';

function copyHandler(copyText) {
    const innerContent = copyText.innerHTML

    function listener(e) {
        e.clipboardData.setData("text/html", innerContent)
        e.clipboardData.setData("text/plain", innerContent)
        e.preventDefault()
    }

    // var range = document.createRange();
    // range.selectNode(ta);
    // window.getSelection().addRange(range);

    // //     
    // try {
    //     document.execCommand('copy');
    // } catch(err) {
    //     console.log('Can`t copy, boss');
    // }
    // //  ,   " "
    // window.getSelection().removeAllRanges();

    document.addEventListener("copy", listener)
    document.execCommand("copy")
    document.removeEventListener("copy", listener)
}

class CopyText extends Component {
    constructor(props){
        super(props)

        this.text = React.createRef()
    }

    componentDidMount() {
        this.renderCopy()
    }

    renderCopy = () => {
        let language = this.props.language,
            code;

        if (language === 'html') {
            code = highlight(this.props.copyText, languages.html)
        } else {
            code = highlight(this.props.copyText, languages.javascript)
        }

        if (this.text.current) {
            this.text.current.innerHTML = code
        }
    }

    render () {
        return (
            <div className={'copy'}>
                <span className="copy__text" ref={this.text}>{this.props.copyText}</span>
                <span className="copy__text-hidden" hidden>{this.props.copyText}</span>
                <span title="" className="copy__button" am-icon="icon_document" onClick={(e) => copyHandler(e.target.closest('.copy').querySelector('.copy__text-hidden'))}></span>
            </div>
        )
    }
}

export default CopyText