import React, { Component } from 'react'
import './Layout.scss'

import Nav from '../components/Nav/Nav.jsx'

export const LayoutContext = React.createContext();

LayoutContext.displayName = 'LayoutContext';

class Layout extends Component {
    constructor(props) {
        super(props)

        this.state = {}
    }

    componentDidMount() {}

    /**
     *  
     * @param {Array} codes  ,   
     */
    initEval(codes = [])  {
        // eslint-disable-next-line no-eval
        codes.forEach(code => eval(code))
    }

    /**
     *      
     * @param {Object} state {stateKey: stateValue}
     * @param {RefObject} ref react ref
     */
    evalEditorJS(stateJs, ref, stateHtml) {
        let stateKey;
        let stateValue;

        let htmlStateKey;
        let htmlStateValue;

        for (const key in stateJs) {
            stateKey = key
            stateValue = stateJs[key]
        }

        if (stateHtml) {
            for (const key in stateHtml) {
                htmlStateKey = key
                htmlStateValue = stateHtml[key]
            }
        }

        if (!stateKey && !stateValue && !ref) {
            return
        }

        this.setState({
            [stateKey]: stateValue
        })

        clearTimeout(this.state.evalTimeout)

        if (this.state.write) {
            this.setState({
                evalTimeout: false
            })
        }

        this.setState({
            write: true,
            evalTimeout: setTimeout(() => {
                try {
                    if (stateHtml) {
                        ref.current.innerHTML = this.state[htmlStateKey];
                    } else {
                        ref.current.innerHTML = '';
                    }

                    console.log(this.state[htmlStateKey]);

                    // eslint-disable-next-line no-eval
                    eval(this.state[stateKey]);

                    this.setState({
                        write: false
                    })
                } catch (error) {
                    ref.current.innerHTML = error;
                }
            }, 200)
        })
    }

    evalEditorHtml(stateHtml, ref, stateJs) {
        try {
            let jsStateKey;
            let jsStateValue;

            let htmlStateKey;
            let htmlStateValue;

            for (const key in stateHtml) {
                htmlStateKey = key
                htmlStateValue = stateHtml[key]
            }

            if (stateJs) {
                for (const key in stateJs) {
                    jsStateKey = key
                    jsStateValue = stateJs[key]
                }
            }

            this.setState({
                [htmlStateKey]: htmlStateValue
            });

            ref.current.innerHTML = htmlStateValue;

            if (jsStateValue) {
                eval(jsStateValue)
            };
        } catch (error) {
            ref.current.innerHTML = error;
        }
    }

    render() {
        const cls = ['layout']

        return (
            <LayoutContext.Provider value={{
                evalEditorJS: this.evalEditorJS,
                initEval: this.initEval,
                evalEditorHtml: this.evalEditorHtml
            }}>
                <div className={cls.join(' ')}>
                    <Nav/>
                    <main className={'main'}>
                        { this.props.children }
                    </main>
                </div>
            </LayoutContext.Provider>
        )
    }
}

export default Layout