import React, { Component, Fragment }               from 'react'
import Editor                                       from 'react-simple-code-editor';
import { highlight, languages }                     from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content                                      from '../../../containers/Content.jsx'
import Header                                       from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider }     from '../../../components/ui/Tabs/'

import CopyText                                     from '../../../components/CopyText/CopyText.jsx';
import { LayoutContext }                            from '../../../containers/Layout.jsx';

import noUiSlider                                   from 'nouislider'
import '../../../../node_modules/nouislider/distribute/nouislider.css';

window.noUiSlider = noUiSlider;

const nouislider_js = `
noUiSlider.create(document.querySelector('[am-slider="sandbox"]'), {
    connect: true,
    behaviour: 'tap',
    start: [100, 900],
    connect: [false, true, false],
    step: 1,
    range: {
        'min': [0],
        'max': [1000]
    }
});
`;

const nouislider_html = `
<div am-slider="sandbox"></div>
`;

const nouislider1_js = `
noUiSlider.create(document.querySelector('[am-slider="sandbox_1"]'), {
    connect: true,
    behaviour: 'tap',
    start: [100],
    connect: [true, false],
    step: 1,
    range: {
        'min': [0],
        'max': [1000]
    }
});
`;

const nouislider2_js = `
noUiSlider.create(this.twoRef.current, {
    connect: true,
    behaviour: 'tap',
    start: [100, 900],
    connect: [false, true, false],
    step: 1,
    range: {
        'min': [0],
        'max': [1000]
    }
});
`;

const nouislider_input = `
const inputMin = document.querySelector('[am-input-range="min"] input')
const inputMax = document.querySelector('[am-input-range="max"] input')

const divForSlider = document.querySelector('[am-slider="input"]')

noUiSlider.create(divForSlider, {
    connect: true,
    behaviour: 'tap',
    start: [1000, 9000],
    connect: [false, true, false],
    step: 1,
    range: {
        'min': [0],
        'max': [10000]
    }
});

divForSlider.noUiSlider.on('update', function (values, handle) {
    let value = String(Math.round(values[handle]));
    // value = value.replace(/\s/g, '');
    // value = value.replace(/(\d)(?=(\d{3})+([^\d]|$))/g, '$1 ');

    if (handle == 1) {
        inputMax.value = value;
    } else if (handle == 0) {
        inputMin.value = value;
    }
});

divForSlider.noUiSlider.on('change', function (values, handle) {
    let value = String(Math.round(values[handle]));
    // value = value.replace(/\s/g, '');
    // value = value.replace(/(\d)(?=(\d{3})+([^\d]|$))/g, '$1 ');
});

inputMin.addEventListener('change', function () {
    divForSlider.noUiSlider.set([this.value.replace(/\s/g, ''), null]);
});

inputMax.addEventListener('change', function () {
    divForSlider.noUiSlider.set([null, this.value.replace(/\s/g, '')]);
});
`;

class Nouislider extends Component {
    static contextType = LayoutContext;

    constructor(props) {
        super(props)

        this.state = {
            nouislider_js,
            nouislider_html,

            write: false,
            evalTimeout: false,
        }


        this.nouisliderRef = React.createRef()
        this.oneRef = React.createRef()
        this.twoRef = React.createRef()
        this.inputRef = React.createRef()
    }

    componentDidMount() {
        this.nouisliderRef.current.innerHTML = this.state.nouislider_html

        this.context.initEval.bind(this)([
            this.state.nouislider_js
        ])

        this.createSlider()
    }

    createSlider = () => {
        noUiSlider.create(this.oneRef.current, {
            connect: true,
            behaviour: 'tap',
            start: [100],
            connect: [true, false],
            step: 1,
            range: {
                'min': [0],
                'max': [1000]
            }
        });

        noUiSlider.create(this.twoRef.current, {
            connect: true,
            behaviour: 'tap',
            start: [100, 900],
            connect: [false, true, false],
            step: 1,
            range: {
                'min': [0],
                'max': [1000]
            }
        });

        // noUiSlider.create(this.inputRef.current, {
        //     connect: true,
        //     behaviour: 'tap',
        //     start: [100, 900],
        //     connect: [false, true, false],
        //     step: 1,
        //     range: {
        //         'min': [0],
        //         'max': [1000]
        //     }
        // });

        this.context.initEval.bind(this)([
            nouislider_input
        ])
    }


    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Nouislider' }
                    headerText={ '.' }
                >
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import noUiSlider from 'nouislider'" />
                    </div>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/nouislider/distribute/nouislider.css';" />
                    </div>
                </Header>
                <Content className="grid">
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <li><TabLink to="sandbox" className="tabs__link" tag="link"></TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <p className="example__about"> <a target="_blank"  rel="noreferrer" href="https://refreshless.com/nouislider/"></a></p>
                                <div am-content="example">
                                    <h2 className="example__header">1 </h2>
                                    <div className="example__preview">
                                        <div ref={this.oneRef}></div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={nouislider1_js}
                                            readOnly={true}
                                            highlight={nouislider1_js => highlight(nouislider1_js, languages.js)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header">2 a</h2>
                                    <div className="example__preview">
                                        <div ref={this.twoRef}></div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={nouislider2_js}
                                            readOnly={true}
                                            highlight={nouislider2_js => highlight(nouislider2_js, languages.js)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview">
                                        <div am-inputs-range={''}>
                                            <div am-input-range="min">
                                                <label>, &nbsp;</label>
                                                <input type="text"/>
                                            </div>
                                            <div am-dots=""><i></i><i></i><i></i></div>
                                            <div am-input-range="max">
                                                <label>, &nbsp;</label>
                                                <input type="text"/>
                                            </div>
                                        </div><br/>
                                        <div ref={this.inputRef} am-slider="input"></div>
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={nouislider_input}
                                            readOnly={true}
                                            highlight={nouislider_input => highlight(nouislider_input, languages.js)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                            </TabContent>
                            <TabContent for="sandbox" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.nouislider_html}
                                            readOnly={false}
                                            onValueChange={nouislider_html => {
                                                this.context.evalEditorHtml.bind(this)({nouislider_html}, this.nouisliderRef, {nouislider_js: this.state.nouislider_js})
                                            }}
                                            highlight={nouislider_html => highlight(nouislider_html, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.nouislider_js}
                                            readOnly={false}
                                            onValueChange={nouislider_js => {
                                                this.context.evalEditorJS.bind(this)({nouislider_js}, this.nouisliderRef,  {nouislider_html: this.state.nouislider_html})
                                            }}
                                            highlight={nouislider_js => highlight(nouislider_js, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                    <div className="example__preview" ref={this.nouisliderRef}></div>
                                </div>
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}

export default Nouislider