import React from 'react'
import './Header.scss'

import { withRouter } from 'react-router-dom'

const Header = props => {
    return (
        <header className={'header'}>
            <div am-container="header">
                { props.headerName ? <h1 className={'header__name'}>{props.headerName}</h1> : null }
                { props.headerText ? <p className={'header__text'}>{props.headerText }</p> : null }
                { props.children }
            </div>

        </header>
    )
}

export default withRouter(Header)