import React, { Component } from 'react'

class TabSlider extends Component {
    onClickHandler = () => {
        //this.props.handleSelect(this.props.nameTab)
    }

    render() {

        return (
            <div className={'tabs__slider'} ref={this.props.sliderRef} style={{width: this.props.widthSlider + 'px', transform: `translateX(${this.props.transformWidthSlider + 'px'})`}}>
                {this.props.children}
            </div>
        )
    }
}

export default TabSlider