import React, { Component, Fragment } from 'react'

import SwiperPage from './Swiper'
import SimpleBarPage from './Simplebar'
import Nouislider from './Nouislider/Nouislider.jsx'
import Photoswipe from './Photoswipe/Photoswipe.jsx'

class PluginsView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            views: {
                swiper: <SwiperPage/>,
                simplebar: <SimpleBarPage/>,
                nouislider: <Nouislider/>,
                photoswipe: <Photoswipe/>,
            },
            renderView: false
        }
    }

    componentDidMount() {
        const view = this.state.views[this.props.match.params.name]

        if (!view) {
            this.props.history.push('/')
        } else {
            this.setState({
                renderView: true
            })
        }
    }

    renderView = () => {
        const name = this.props.match.params.name
        const views = {...this.state.views}

        for(let view in views) {
            if (name === view) {
                return (
                    views[view]
                )
            }
        }
    }

    render() {

        return (
            <Fragment>
                { this.state.renderView && this.renderView() }
            </Fragment>
        )
    }
}

export default PluginsView