import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../containers/Content.jsx'
import Header from '../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../components/ui/Tabs/'

const npm = `
npm i pobeda-ui
`;

const webpack = `
resolve: {
    alias: {
        '@ui': path.resolve(__dirname, '/node_modules/pobeda-ui/src/ui/js'),
        '@ui/scss': path.resolve(__dirname, '/node_modules/pobeda-ui/src/ui/scss'),
        '@utils': path.resolve(__dirname, '/node_modules/pobeda-ui/src/ui/utils/utils'),
    }
}
`;

const code = `
import ui from '@ui'
import utils from '@utils'
import Accordion from '.@ui/Accordion'
 
const accordion = new Accordion('[am-accordion="simple"]')
`;

const component = `
const accordion = new Accordion('[am-accordion="multiple"]', {
    duration: 800,
    multiple: true,
    init: false
})
 
accordion.init()
 
accordion.on('init', (data) => {
    console.log(data);
})
 
accordion.on('change', (tab, panel) => {
    console.log(tab, panel);
})
`;

const scss = `
@import './yourVariablesComponent.scss';
@import 'node_modules/pobeda-ui/src/ui/scss/am-accordion.scss';
`;

class Main extends Component {
    constructor(props) {
        super(props)

        this.state = {
            npm,
            webpack,
            code,
            component,
            scss
        }

        // this.npm = React.createRef()
    }

    componentDidMount() {
        // this.npm.current.innerHTML = this.state.npm;
        // this.props.history.push('/markup/grid')
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ ' ' }
                    headerText={ '     ' }
                >
                </Header>
                <Content>
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header">npm</h2>
                                    {/* <div className="example__preview" ref={this.npm}></div> */}
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.npm}
                                            onValueChange={npm => { this.setState({ npm }); this.npm.current.innerHTML = npm}}
                                            readOnly={true}
                                            highlight={npm => highlight(npm, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div> 
                                <div am-content="example">
                                    <h2 className="example__header">webpack</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.webpack}
                                            readOnly={true}
                                            highlight={webpack => highlight(webpack, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div> 
                                <div am-content="example">
                                    <h2 className="example__header">code</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.code}
                                            readOnly={true}
                                            highlight={code => highlight(code, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div> 
                                <div am-content="example">
                                    <h2 className="example__header">component</h2>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.component}
                                            readOnly={true}
                                            highlight={component => highlight(component, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div> 
                                <div am-content="example">
                                    <h2 className="example__header">scss</h2>
                                    <p className="example__about">Add component styles to your local styles file</p>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.scss}
                                            readOnly={true}
                                            highlight={scss => highlight(scss, languages.javascript)}
                                            padding={0}
                                        />
                                    </div>
                                </div>                                
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}

export default Main