import React, { Component } from 'react'
import './Tabs.scss'

const findDefaultTab = (children, width = false) => {
    let firstLink;
        // firstDefaultLink;

    const findTab = child => {
        if (!child || !child.props /*!child || !child.props || firstDefaultLinklastLink*/) {
            return;
        }

        if (child.props.tag === 'link') {
            firstLink = firstLink || child.props.to;
            //firstDefaultLink = firstDefaultLink || (child.props.default && child.props.to);
        }

        React.Children.forEach(child.props.children, findTab);
    }

    React.Children.forEach(children, findTab);

    return /*firstDefaultLink || */firstLink;
}

class Tabs extends Component {
    constructor(props) {
        super(props)

        this.state = {
            selectedTab: this.props.selectedTab || findDefaultTab(this.props.children),
            widthSlider: findDefaultTab(this.props.children, true) || 0,
            transformWidthSlider: 0,
            timeoutSlider: false
        }

        this.tabs = React.createRef()
        this.sliderRef = React.createRef()
    }

    transformChildren(children, { handleSelect, handleSlider }) {
        if (typeof children !== 'object') {
            return children
        }

        return React.Children.map(children, child => {
            if (!child) {
                return child
            }
            // if (child.props.className == 'tabs__link') {
            //     console.log(child);
            // }
            //if (child.type.name === 'TabLink') {

            if (child.props.tag === 'link') {
                return React.cloneElement(child, {
                    active: child.props.to === this.state.selectedTab,
                    nameTab: child.props.to,
                    handleSelect,
                    handleSlider
                })
            }

            if (child.props.tag === 'content') {
                return React.cloneElement(child, {
                    active: child.props.for === this.state.selectedTab,
                })
            }

            if (child.props.tag === 'slider') {
                return React.cloneElement(
                    child,
                    {
                        handleSlider,
                        widthSlider: this.state.widthSlider,
                        transformWidthSlider: this.state.transformWidthSlider,
                        sliderRef: this.sliderRef
                    },
                    this.transformChildren(child.props.children, { handleSelect, handleSlider }),
                );
            }

            return React.cloneElement(
                child,
                {},
                this.transformChildren(child.props.children, { handleSelect, handleSlider }),
            );
        });
    }

    handleSelect = tabName => {
        this.setState({
            selectedTab: tabName,
        })
    }

    handleSlider = (opt, first = false) => {
        //      \
        if (first) {
            this.setState({
                timeoutSlider: setTimeout(() => {
                    this.sliderRef.current.style.transitionDuration = '0s';
                    this.setState({
                        widthSlider: opt.width,
                        transformWidthSlider: opt.link.left - this.tabs.current.getBoundingClientRect().left
                    });
                }, 300)
            })
        } else {
            this.sliderRef.current.style.transitionDuration = '.3s';
            this.setState({
                widthSlider: opt.width,
                transformWidthSlider: opt.link.left - this.tabs.current.getBoundingClientRect().left
            });
        }
    }

    componentWillUnmount() {
        clearTimeout(this.state.timeoutSlider)
    }

    render() {
        const handleSelect = this.handleSelect
        const handleSlider  = this.handleSlider
        const children = this.transformChildren(this.props.children, { handleSelect, handleSlider })

        return (
            <div className={this.props.className} ref={this.tabs}>
                {children}
            </div>
        )
    }
}

export default Tabs