import React, { Component, Fragment } from 'react'
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-markup';
import 'prismjs/themes/prism-coy.css';

import Content from '../../../containers/Content.jsx'
import Header from '../../../components/Header/Header.jsx'
import { Tabs, TabContent, TabLink, TabSlider } from '../../../components/ui/Tabs/'
import CopyText from '../../../components/CopyText/CopyText.jsx'

const radio = `
<div am-radio>
    <input am-radio-input type="radio">
    <div am-radio-wrapper>
        <div am-radio-icon></div>
        <div am-radio-label>  </div>
    </div>
</div>

<div am-radio>
    <input type="radio">
    <label am-radio-wrapper>
        <span am-radio-icon></span>
        <span am-radio-label>  </span>
    </label>
</div>

<div am-radio>
    <input type="radio" checked>
    <span am-radio-icon></span>
    <span am-radio-label>  </span>
</div>
`;

const radio_group = `
<div am-radio>
    <input am-radio-input name="sale" type="radio">
    <div am-radio-wrapper>
        <div am-radio-icon></div>
        <div am-radio-label>  </div>
    </div>
</div>

<div am-radio>
    <input type="radio" name="sale">
    <label am-radio-wrapper>
        <span am-radio-icon></span>
        <span am-radio-label>  </span>
    </label>
</div>

<div am-radio>
    <input type="radio" name="sale" checked>
    <span am-radio-icon></span>
    <span am-radio-label>  </span>
</div>
`;

const radio_disabled = `
<div am-radio disabled>
    <input am-radio-input name="sale" type="radio" disabled>
    <div am-radio-wrapper>
        <div am-radio-icon></div>
        <div am-radio-label>  </div>
    </div>
</div>

<div am-radio disabled>
    <input type="radio" name="sale" disabled>
    <label am-radio-wrapper>
        <span am-radio-icon></span>
        <span am-radio-label>  </span>
    </label>
</div>

<div am-radio disabled>
    <input type="radio" name="sale" checked disabled>
    <span am-radio-icon></span>
    <span am-radio-label>  </span>
</div>
`;

const scssVariables = `
// Colors
$color-main-blue-dark: #091F7C !default;
$color-main-blue-blur: #475077 !default;
`;

export class Radio extends Component {
    constructor(props) {
        super(props)

        this.state = {
            radio,
            radio_group,
            radio_disabled,
            scssVariables
        }

        this.radio = React.createRef()
        this.radio_group = React.createRef()
        this.radio_disabled = React.createRef()
    }

    componentDidMount() {
        this.radio.current.innerHTML = this.state.radio;
        this.radio_group.current.innerHTML = this.state.radio_group;
        this.radio_disabled.current.innerHTML = this.state.radio_disabled;
    }

    render() {
        return (
            <Fragment>
                <Header
                    headerName={ 'Radio' }
                    headerText={ ' .' }
                >                    
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>scss:</span> <CopyText language="scss" copyText="@import 'node_modules/pobeda-ui/src/ui/scss/am-radio.scss';" />  
                    </div>        
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <span style={{marginTop: '9px', marginRight: '10px'}}>js:</span> <CopyText language="js" copyText="import '@ui/scss/am-radio.scss'" />  
                    </div>
                </Header>
                <Content>
                    <div className="example">
                        <Tabs selectedTab="examples" className="tabs">
                            <ul className="tabs__links">
                                <li><TabLink to="examples" className="tabs__link" tag="link">  </TabLink></li>
                                <TabSlider tag="slider"/>
                            </ul>
                            <TabContent for="examples" className="tabs__content" tag="content">
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.radio}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.radio}
                                            readOnly={true}
                                            onValueChange={radio => { this.setState({ radio }); this.radio.current.innerHTML = radio}}
                                            highlight={radio => highlight(radio, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"> </h2>
                                    <div className="example__preview" ref={this.radio_group}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.radio_group}
                                            readOnly={true}
                                            onValueChange={radio_group => { this.setState({ radio_group }); this.radio_group.current.innerHTML = radio_group}}
                                            highlight={radio_group => highlight(radio_group, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <div am-content="example">
                                    <h2 className="example__header"></h2>
                                    <div className="example__preview" ref={this.radio_disabled}></div>
                                    <div className="example__code">
                                        <Editor
                                            value={this.state.radio_disabled}
                                            readOnly={true}
                                            onValueChange={radio_disabled => { this.setState({ radio_disabled }); this.radio_disabled.current.innerHTML = radio_disabled}}
                                            highlight={radio_disabled => highlight(radio_disabled, languages.markup)}
                                            padding={0}
                                        />
                                    </div>
                                </div>
                                <h2 className="example__header">scss .</h2>
                                <div className="example__code">
                                    <Editor
                                            value={this.state.scssVariables}
                                            onValueChange={scssVariables => {
                                                this.setState({ scssVariables });
                                            }}
                                            readOnly={true}
                                            highlight={scssVariables => highlight(scssVariables, languages.javascript)}
                                            padding={0}
                                        />
                                </div> 
                            </TabContent>
                        </Tabs>
                    </div>
                </Content>
            </Fragment>
        )
    }
}